-->
<template>
  <a-form-model>
    <a-form-model-item label="">
      <a-radio-group
        buttonStyle="solid"
        v-model="config.layout"
      >
        <a-radio-button value="horizontal"></a-radio-button>
        <a-radio-button value="vertical"></a-radio-button>
      </a-radio-group>
    </a-form-model-item>
    <a-form-model-item label="">
      <a-input
        allowClear
        v-model="config.width"
      ></a-input>
    </a-form-model-item>
    <a-form-model-item style='margin-bottom:0'>
      
      <a-tooltip title="">
        <a-icon
          type="undo"
          @click="handleReset"
          style="margin-left:5px;cursor:pointer"
        />
      </a-tooltip>
    </a-form-model-item>
    <a-form-model-item>
      <a-slider
        :max="24"
        :min="0"
        :value="config.labelCol.span || 2"
        @change="handleSliderChange"
      />
    </a-form-model-item>
    <a-form-model-item label="">
      <a-radio-group
        buttonStyle="solid"
        v-model="config.labelAlign"
      >
        <a-radio-button value="left"></a-radio-button>
        <a-radio-button value="right"></a-radio-button>
      </a-radio-group>
    </a-form-model-item>
    <a-form-model-item label="">
      <a-radio-group
        buttonStyle="solid"
        v-model="config.size"
      >
        <a-radio-button value="small"></a-radio-button>
        <a-radio-button value="default"></a-radio-button>
        <a-radio-button value="large"></a-radio-button>
      </a-radio-group>
    </a-form-model-item>
    <a-form-model-item label="">
      <a-input
        allowClear
        v-model="config.separator"
      ></a-input>
    </a-form-model-item>
    <a-form-model-item label="">
      <a-checkbox v-model="config.colon"> </a-checkbox>
    </a-form-model-item>
  </a-form-model>
</template>

<script>
export default {
  props: {
    config: { type: Object },
  },
  data () {
    return {};
  },
  created () {},
  mounted () {},
  methods: {
    // (bus)
    handleSliderChange (val) {
      this.$formDesignBus.$emit('changeFormConfig', {
        labelCol: { span: val },
        wrapperCol: { span: 24 - val },
      });
    },
    handleReset () {
      this.$formDesignBus.$emit('resetFormConfigCols');
    },
  },
};
</script>
<style scoped lang="less">
.response-inputs-wrapper {
  display: flex;
  flex-wrap: wrap;
  .response-input-item {
    width: 50%;
    .item-label {
      display: inline-block;
      width: 30px;
      margin-right: 5px;
      text-align: right;
      &::after {
        content: ":";
      }
    }
  }
}
</style>
