<template>
  <a-form>
    <a-form-item label="">
      <a-input
        allowClear
        v-model="formData.options.label"
      ></a-input>
    </a-form-item>
    <a-form-item label="">
      <a-input
        allowClear
        v-model="formData.model"
      ></a-input>
    </a-form-item>
    <a-form-item label="">
      <a-switch v-model="formData.options.defaultValue" />
    </a-form-item>
    <a-form-item label="">
      <a-checkbox v-model="formData.options.disabled"></a-checkbox>
    </a-form-item>
    <a-divider></a-divider>
    <a-form-item label="">
      <a-checkbox v-model="formData.options.required"></a-checkbox>
      <a-input
        v-if="formData.options.required"
        v-model="formData.options.requiredMsg"
        placeholder=""
      ></a-input>
    </a-form-item>
  </a-form>
</template>
<script>
export default {
  props: {
    formData: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
};
</script>
