// -vue
export const buildVueComTpl = (jsonStr) => {
  return `<template>
  <div>
    <form-build
      ref="formBuildElem"
      :widget-form-data="widgetFormData"
    ></form-build>
    <div style="text-align:center">
    <a-button
      @click="getData"
      type="primary"
    ></a-button>
  </div>
  </div>
</template>
<script>
export default {
  name: 'FormBuild',
  data () {
    return {
      widgetFormData: ${jsonStr}
    }
  },
  methods: {
     getData() {
       // 
       this.$refs['formBuildElem'].handleSubmit().then((data) => {
        console.log(data);
      }).catch(err => {
           console.log(err, '')
         })
     }
  }
}
</script>`;
};
// html
export const buildHtmlComTpl = (jsonStr) => {
  return `<!DOCTYPE html>
  <html>
  <head>
    <title></title>
    <meta charset="UTF-8">
    <link rel="stylesheet" href="https://unpkg.com/zd-form-build@0.1.0/lib/form-build.css">
  </head>
  
  <body>
    <div id="app">
    <form-build
    ref="formBuildElem"
    :widget-form-data="widgetFormData"
  ></form-build>
  <div style="text-align:center">
  <a-button
    @click="getData"
    type="primary"
  ></a-button>
</div>
    </div>
  </body>
  <script src="https://cdn.jsdelivr.net/npm/vue@2.6.12"></script>
  <script src="https://unpkg.com/zd-form-build@0.1.0/lib/form-build.umd.min.js"></script>
  <script>
    const vm = new Vue({
      el: '#app',
      data: {
        widgetFormData: ${jsonStr}
      },
      methods: {
        getData() {
          // 
          this.$refs['formBuildElem'].handleSubmit().then((data) => {
           console.log(data);
         }).catch(err => {
              console.log(err, '')
            })
        }
      }
    })
  </script>
  </html>`
}

