//  v-clipboard : copy= cut cutinput  textarea success  error 
import app from '@/main.js' // TODO vue
const Clipboard = require('clipboard'); // http://www.clipboardjs.cn/
if (!Clipboard) {
  throw new Error('npm install `clipboard` --save');
}
// https://cn.vuejs.org/v2/guide/custom-directive.html
export default {
  bind (el, binding) {
    const { arg, value } = binding;
    if (arg === 'success' || arg === 'error') {
      el[`_v_clipboard_${arg}`] = value;
    } else {
      // 
      const clipboard = new Clipboard(el, {
        text () { return value },
        action () { return arg === 'cut' ? 'cut' : 'copy' }
      });
      let optStr = arg === 'cut' ? '' : '';
      clipboard.on('success', e => {
        const callback = el._v_clipboard_success;
        if (callback) {
          callback(e);
        } else {
          app.$message.destroy();
          app.$message.success(`${optStr}`);
        }
      })
      clipboard.on('error', e => {
        const callback = el._v_clipboard_error;
        if (callback) {
          callback(e);
        } else {
          app.$message.destroy();
          app.$message.success(`${optStr}`);
        }
      })
      el._v_clipboard = clipboard
    }
  },
  update (el, binding) {
    const { arg, value } = binding;
    if (arg === 'success' || arg === 'error') {
      el[`_v_clipboard_${arg}`] = value;
    } else {
      el._v_clipboard.text = function () { return value }
      el._v_clipboard.action = function () { return arg === 'cut' ? 'cut' : 'copy' }
    }
  },
  unbind (el, binding) {
    const { arg } = binding;
    if (arg === 'success' || arg === 'error') {
      delete el[`_v_clipboard_${arg}`];
    } else {
      el._v_clipboard.destroy();
      delete el._v_clipboard
    }
  }
}
